import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	 components:{
	        wPicker
	    },
	data() {
		return {
			oriVisitId:0,
			customerName:"",
			customerMobile:'',
			employeeName:'',
			houseName:"",
			visitTime:"",
			content:"",
			paimaiId:"",
			houseSource:"",
			hasclickback:false
		}
	},
	onShow() {
		console.log(this.userId)
	},
	methods: {
		bindTextAreaBlur(e){
			this.content=e.detail.value;
		},
		datePicker: function(e) {
			const that = this;
			that.$refs.date.show();
		},
		onConfirmDate(val){
			const that= this;
			that.visitTime=val.result;
		},
		sub(){
			const that = this;
			setTimeout(()=>{
				if(that.oriVisitId==0||that.content==""||that.visitTime==""){
					uni.showToast({
						title:"请输入必填内容",
						icon:"none"
					})
				}
				else{	
					uni.showModal({
						content: "回访提交后将不可再次修改，确认提交日志？",
						confirmText: "确认提交",
						cancelText: "取消",
						success: function (res) {
							if (res.confirm) {
								uni.showLoading({
									title:"提交中..."
								})
								let param = {
									oriVisitId: that.oriVisitId,
									visitTime:that.visitTime,
									content:that.content
								}
								that.$api.returnvisitAdd(param).then(res => {
									if (res.success) {
										uni.showToast({
											title:"提交成功"
										})
										setTimeout(()=>{
											that.$Router.back(1);
										},1000)	
										var pages = getCurrentPages(); //当前页
										var beforePage = pages[pages.length - 2].route; //上个页面
										if(beforePage=="pages/my/manageNavs/returnVisit/list"){
											that.prevPageReload();
										}	
									} else {
										uni.showToast({
											title: res.message,
											icon: "none"
										})
									}
								})
								// uni.showToast({
								// 	title:"提交成功"
								// })
							} else if (res.cancel) {
								
							}	
						}
					});	
					
				}
			},100)
		},
		searchVisit(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/searchVisit"
			})
		},
		prevPageReload(){
			var pages = getCurrentPages(); //当前页
			var beforePage = pages[pages.length - 2]; //上个页面




			beforePage.onLoad()

		},
		back(){
			const that = this;
			if(that.oriVisitId!=0||that.content!=""||that.visitTime!=""){	
				uni.showModal({
						content: "退出后将不保留已编辑的内容，确认退出日志编辑？",
						confirmText: "继续编辑",
						cancelText: "退出",
					success: function (res) {
						if (res.confirm) {
				
						} else if (res.cancel) {
							that.$Router.back(1);
						}	
					}
				});		
			}
			else{
				that.$Router.back(1);
			}		
		}
	},
	
}
